import {Ishod} from './Ishod';
import stil from './stil-mrtav.css';

const akcije = ['Pokuaj ponovo!', 'Pokuaj opet', 'Probaj iznova', 'Okuaj se jo jednom']
const ishodi = [
  'Hrabro si pao u borbi protiv okupatora. Tvoje telo e pojesti crvi, ali seanje na tvoju borbu trajae veno.',
  'Ubijen si u borbi sa jaim. Tvoje kosti sada raznose zveri, ali tvoja hrabrost nadahnjuje generacije.',
  'Ubijen si od dumanske ruke. Tvoj grob e ostati neznan, ali tvoji su podvizi proslavljeni pesmom.'
]

export class Mrtav extends Ishod {

  constructor(podaci) {
    super(podaci);
    this.stil = stil;
    this.podaci.naslov = podaci.naslov || 'Mrtav si'
    this.podaci.slika = podaci.slika || 'fotke/smrt/jajinci-doubijanje.jpg'
    this.podaci.opis = podaci.opis || ishodi[Math.floor(Math.random() * ishodi.length)]
    this.podaci.akcija = podaci.akcija || akcije[Math.floor(Math.random() * akcije.length)]
    this.podaci.link = podaci.link || ''
  }

}



